.dark {
  visibility: hidden;
  position: fixed;
  z-index: 1998;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($dark, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  &--visible {
    opacity: 1;
    visibility: visible; } }
.modal {
  visibility: hidden;
  position: fixed;
  z-index: 1999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 506px;
  max-height: 509px;
  display: flex;
  align-items: center;
  justify-content: center;
  &--visible {
    opacity: 1;
    visibility: visible; }
  &__dialog {
    position: relative;
    max-width: 36.6rem;
    background: $dark url('../img/bg-lines.svg');
    background-position: center;
    background-repeat: no-repeat;
    padding: 2.357rem 3.5rem 1.982rem;
    color: $light; }
  &__close {
    position: absolute;
    right: 0;
    top: 0;
    width: 28px;
    height: 28px;
    background: transparent url('../img/x-circle.svg');
    background-position: center;
    background-repeat: no-repeat;
    border: none;
    margin: 0.9rem;
    margin-top: 0.7rem;
    margin-right: 1rem;
    cursor: pointer; }
  &__title {
    font-family: 'Yeseva One', cursive;
    font-size: 1.357rem;
    font-weight: normal;
    line-height: 1.9rem;
    text-align: center;
    margin: 0;
    margin-bottom: 1.95rem; }
  &__form {
    display: flex;
    flex-direction: column; }
  &__input {
    margin-bottom: 3rem;
    outline: none;
    font-weight: 500;
    &:last-of-type {
      margin-bottom: 1rem; } }
  &__policy {
    margin-bottom: 2.7rem; }
  &__button {
    align-self: flex-end;
    padding: 1.35rem 3.2rem;
    font-size: 12px;
    line-height: 13px;
    font-family: 'Futura DemiC', sans-serif;
    font-weight: 600; } }
