.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 99;
  background-color: $dark;
  padding: 24px 0; }
.menu {
  display: flex;
  justify-content: space-between;
  align-items: center; }
.logo {
  color: $primary;
  text-transform: uppercase;
  font-size: 1.075rem; }
.nav {
  &__item {
    color: $light;
    text-decoration: none;
    text-transform: uppercase;
    margin-right: 2.5rem;
    &:hover {
      color: $primary;
      transition: 0.2s; } } }
.phone {
  color: $primary; }
.contacts {
  &__phone {
    margin-right: 40px; } }
